	module.exports = {"data":{"markdownRemark":{"html":"<h2>Getting started</h2>\n<!-- {.-three-column} -->\n<h3>Exiting</h3>\n<!-- {.-prime} -->\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>:qa</code></td>\n<td>Close all files</td>\n</tr>\n<tr>\n<td><code>:qa!</code></td>\n<td>Close all files, abandon changes</td>\n</tr>\n<tr>\n<td>---</td>\n<td>---</td>\n</tr>\n<tr>\n<td><code>:w</code></td>\n<td>Save</td>\n</tr>\n<tr>\n<td><code>:wq</code>\n \n<em>/</em>\n \n<code>:x</code></td>\n<td>Save and close file</td>\n</tr>\n<tr>\n<td>---</td>\n<td>---</td>\n</tr>\n<tr>\n<td><code>:q</code></td>\n<td>Close file</td>\n</tr>\n<tr>\n<td><code>:q!</code></td>\n<td>Close file, abandon changes</td>\n</tr>\n<tr>\n<td>---</td>\n<td>---</td>\n</tr>\n<tr>\n<td><code>ZZ</code></td>\n<td>Save and quit</td>\n</tr>\n<tr>\n<td><code>ZQ</code></td>\n<td>Quit without checking changes</td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h3>Navigating</h3>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>h</code>\n \n<code>j</code>\n \n<code>k</code>\n \n<code>l</code></td>\n<td>Arrow keys</td>\n</tr>\n<tr>\n<td><code>&#x3C;C-U></code>\n \n<em>/</em>\n \n<code>&#x3C;C-D></code></td>\n<td>Page up/page down</td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h4>Words</h4>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>b</code>\n \n<em>/</em>\n \n<code>w</code></td>\n<td>Previous/next word</td>\n</tr>\n<tr>\n<td><code>e</code>\n \n<em>/</em>\n \n<code>ge</code></td>\n<td>Previous/next end of word</td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h4>Line</h4>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>0</code>\n \n<em>(zero)</em></td>\n<td>Start of line</td>\n</tr>\n<tr>\n<td><code>^</code></td>\n<td>Start of line \n<em>(after whitespace)</em></td>\n</tr>\n<tr>\n<td><code>$</code></td>\n<td>End of line</td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h4>Document</h4>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>gg</code></td>\n<td>First line</td>\n</tr>\n<tr>\n<td><code>G</code></td>\n<td>Last line</td>\n</tr>\n<tr>\n<td><code>:n</code></td>\n<td>Go to line \n<code>n</code></td>\n</tr>\n<tr>\n<td><code>nG</code></td>\n<td>Go to line \n<code>n</code></td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h3>Editing</h3>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>a</code></td>\n<td>Append</td>\n</tr>\n<tr>\n<td><code>i</code></td>\n<td>Insert</td>\n</tr>\n<tr>\n<td><code>o</code></td>\n<td>Next line</td>\n</tr>\n<tr>\n<td><code>O</code></td>\n<td>Previous line</td>\n</tr>\n<tr>\n<td>---</td>\n<td>---</td>\n</tr>\n<tr>\n<td><code>s</code></td>\n<td>Delete char and insert</td>\n</tr>\n<tr>\n<td><code>S</code></td>\n<td>Delete line and insert</td>\n</tr>\n<tr>\n<td><code>C</code></td>\n<td>Delete until end of line and insert</td>\n</tr>\n<tr>\n<td>---</td>\n<td>---</td>\n</tr>\n<tr>\n<td><code>r</code></td>\n<td>Replace one character</td>\n</tr>\n<tr>\n<td><code>R</code></td>\n<td>Enter Replace mode</td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h3>Exiting insert mode</h3>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>Esc</code>\n \n<em>/</em>\n \n<code>&#x3C;C-[></code></td>\n<td>Exit insert mode</td>\n</tr>\n<tr>\n<td><code>&#x3C;C-C></code></td>\n<td>Exit insert mode, and abort current command</td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h3>Clipboard</h3>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>x</code></td>\n<td>Delete character</td>\n</tr>\n<tr>\n<td>---</td>\n<td>---</td>\n</tr>\n<tr>\n<td><code>dd</code></td>\n<td>Delete line \n<em>(Cut)</em></td>\n</tr>\n<tr>\n<td><code>yy</code></td>\n<td>Yank line \n<em>(Copy)</em></td>\n</tr>\n<tr>\n<td>---</td>\n<td>---</td>\n</tr>\n<tr>\n<td><code>p</code></td>\n<td>Paste</td>\n</tr>\n<tr>\n<td><code>P</code></td>\n<td>Paste before</td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h3>Visual mode</h3>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>v</code></td>\n<td>Enter visual mode</td>\n</tr>\n<tr>\n<td><code>V</code></td>\n<td>Enter visual line mode</td>\n</tr>\n<tr>\n<td><code>&#x3C;C-V></code></td>\n<td>Enter visual block mode</td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<h4>In visual mode</h4>\n<table>\n<thead>\n<tr>\n<th>Shortcut</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>d</code>\n \n<em>/</em>\n \n<code>x</code></td>\n<td>Delete selection</td>\n</tr>\n<tr>\n<td><code>s</code></td>\n<td>Replace selection</td>\n</tr>\n<tr>\n<td><code>y</code></td>\n<td>Yank selection \n<em>(Copy)</em></td>\n</tr>\n</tbody>\n</table>\n<!-- {.-shortcuts} -->\n<p>See <a href=\"#operators\">Operators</a> for other things you can do.</p>\n<h2>Operators</h2>\n<!-- {.-three-column} -->\n<h3>Usage</h3>\n<!-- {.-prime} -->\n<p>Operators let you operate in a range of text (defined by <em>motion</em>). These are preformed in normal mode.</p>\n<!-- {.-setup} -->\n<p>SNIP</p>\n<h2>Also see</h2>\n<ul>\n<li><a href=\"https://vim.rtorr.com/\">Vim cheatsheet</a> <em>(vim.rotrr.com)</em></li>\n<li><a href=\"http://vimdoc.sourceforge.net/htmldoc/\">Vim documentation</a> <em>(vimdoc.sourceforge.net)</em></li>\n<li><a href=\"http://openvim.com/\">Interactive Vim tutorial</a> <em>(openvim.com)</em></li>\n</ul>","frontmatter":{"path":"/vim","title":"Vim","intro":"[Vim](http://vim.org) is a text editor.\n"}}},"pathContext":{}}

